@import-normalize; 

body {
  margin: 0;
  line-height: 1.5;
  font-family: Arial, Helvetica, sans-serif;
  background-color: rgba(245, 255, 238, 0.771);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}